//             }
//             ,
//             quantity: 1
//         }
//     ]
// };
const store = createStore
    (
        createReduer, loadStorge(),
        compose(
            applyMiddleware(thunk),
            window.__REDUX_DEVTOOLS_EXTENSION__
                ? window.__REDUX_DEVTOOLS_EXTENSION__()
                : f => f
        )
    );
store.subscribe(() => {
    saveState(store.getState())
})
export default store;